import Vue from 'vue' //vue
import Vuex from 'vuex' //vuex
import storeOption from './store' //vuex
import VueRouter from 'vue-router' //
import routes from './routes' //
import App from './App' //app
import util from 'assets/js/util.js' //

import FastClick from 'fastclick' //300ms
import ECharts from 'vue-echarts' //echart
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
 //vuxUI
import VueScroller from 'vue-scroller' //
import _ from 'lodash' //lodash
import VeeValidate, {
	Validator
} from 'vee-validate'; //
import veeValidateRule from 'assets/js/veeValidateRule.js' //VeeValidate
import zh_CN from 'vee-validate/dist/locale/zh_CN'; //VeeValidate
Validator.addLocale(zh_CN);
Object.keys(veeValidateRule).forEach((t, i) => { //
	Validator.extend(t, veeValidateRule[t]);
})
Validator.updateDictionary({ //
	'zh_CN': {
		messages: {
			required: (aa) => ''
		},
		numeric: {
			required: () => ''
		}
	}
});
Vue.use(VeeValidate, {
	errorBagName: 'validateError',
	fieldsBagName: 'validateFields',
	locale: 'zh_CN'
});

Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(VueScroller)
Vue.component('chart', ECharts)
Vue.use(ToastPlugin)
Vue.use(DatetimePlugin)
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)
//Vue.use(_)
FastClick.attach(document.body)
Vue.config.productionTip = false
Vue.config.errorHandler = function(err, vm) {
	console.log(err, vm)
}
//  store 
const store = new Vuex.Store(storeOption);
// 
const router = new VueRouter({
	//mode: 'history',
	routes: routes,
	hashbang: true
});

Vue.prototype.$common = util;
window.homeCareApp = new Vue({
	router,
	store,
	render: h => h(App)
}).$mount('#app-box');
const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
router.beforeEach((to, from, next) => {
	homeCareApp.$vux.loading.hide();
	document.body.scrollTop = 0;
	if(to.matched.some(record => record.meta.noRequiresAuth)) {
		next()
	} else {
		if(!localStorage.getItem('token')) {
			next({
				path: '/login'
			})
		} else {
			next()
		}
	}
	//
	const toIndex = history.getItem(to.path)
	const fromIndex = history.getItem(from.path)
	if(toIndex) {
		if(!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
			 store.commit('updateDirection', {direction: 'forward'})
		} else {
			 store.commit('updateDirection', {direction: 'reverse'})
		}
	} else {
		++historyCount
		history.setItem('count', historyCount)
		to.path !== '/' && history.setItem(to.path, historyCount)
		store.commit('updateDirection', {direction: 'forward'})
	}
	if(/\/http/.test(to.path)) {
		let url = to.path.split('http')[1]
		window.location.href = `http${url}`
	} else {
		next()
	}
})
router.afterEach(function(to) {
//	setTimeout(() => {
//		
//		if(document.querySelector('.common-header-left')) {
//			document.querySelector('.common-header-left').classList.add('active')
//		}
//		if(document.querySelector('.common-header-right')) {
//			document.querySelector('.common-header-right').classList.add('active')
//		}
//	}, 500)

})
if(!localStorage.getItem('token')) {
	homeCareApp.$router.push('/login')
}

//openId,openId
localStorage.setItem('openId', location.search.split('&')[0].split('=')[1])
homeCareApp.$common.login({
	loading:false,
	noMessage:true,//
	render: () => {
		if(!homeCareApp.$router.currentRoute.meta.messageLink){
			homeCareApp.$router.push('/homeRouterView/home')
		}
	}
})


