
	import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

	import sha512 from 'js-sha512' //sha512
import mixins from 'assets/js/mixins.js'
export default {
		data() {
			return {
				tel: '',
				vCode: '',
				password: '',
				password2: '',
				telLoading: false,
				vCodeStart: false, //
				time: 60, //
				cityList: [], //json
				telNoRepeat: false //
			}
		},
		mixins: [mixins.city],
		watch: {
			tel(opt) {
				if(opt.length == 11) {
//					this.checkLoginName();
				}
			}
		},
		computed: {
			//
			isEqually() {
				return this.password == this.password2 ? true : false
			}

		},
		methods: {
			//
			checkLoginName() {
				this.telLoading = true;
				this.telNoRepeat = false;
				this.$validator.validateAll({
					tel: this.tel
				}).then(() => {
					this.$common.ajax({
						loading: false,
						data: {
							header: {
								action: 'CheckMobile',
							},
							body: {
								mobile: this.tel,
							}
						},
						success: (res) => {
							this.telLoading = false;
							this.$vux.toast.show({
								text: '',
								type: 'cancel'
							});
						},
						error: (res) => {
							this.telLoading = false;
							this.telNoRepeat = false;
						}
					})
				}).catch(() => {
					this.telLoading = false;
				});
			},
			//
			getPhoneValidateCode() {
				this.$validator.validateAll({
					tel: this.tel
				}).then(() => {
//					if(!this.telNoRepeat) {
						this.$common.ajax({
							loadingText: '',
							data: {
								header: {
									action: 'SmsValidateCode',
								},
								body: {
									mobile: this.tel,
								}
							},
							success: (res) => {
								this.vCodeStart = true;
								this.$vux.toast.show({
									text: '' + this.tel + '',
									width: '50%',
									type: 'text'
								})
							}
						})
//					} else {
//						this.$vux.toast.show({
//							text: '',
//							type: 'cancel'
//						});
//					}
				})
			},
			//
			finish() {
				this.vCodeStart = false;
				this.time = 60;
			},
			//
			submitData() {
				if(!this.telNoRepeat) {
					if(this.isEqually) {
						this.$common.ajax({
							data: {
								header: {
									action: 'EditPasswordByValidateCode',
								},
								body: {
									mobile: this.tel,
									validatecode: this.vCode,
									newpassword: sha512(this.password)
								}
							},
							success: (res) => {
								this.$vux.toast.show({
									text: res.cause,
									onHide: () => {
										this.$router.push({
											path: '/login'
										});
									}
								})
							}
						})
					} else {
						this.$vux.toast.show({
							text: '',
							type: 'cancel'
						});
					}
				} else {
					this.$vux.toast.show({
						text: '',
						type: 'text'
					});
				}
			},
			//
			login() {
				this.$common.login({
					action:'DoctorLoginByValidateCode',
					loading: true,
					tel: this.tel,
					ValidateCode:this.vCode,
					render: () => {
						this.$router.push({
							path: '/homeRouterView/home'
						});
						if(!localStorage.cityList) {
							this.getCityList();
						}
					}
				})
			},
		},
		mounted() {
			this.$validator.validateAll()
		},
		components: {
			XInput,
			Group,
			XButton,
			XHeader,
			Countdown,
			Spinner
		}
	}



